
.fade-enter {
  opacity: 0;
}
.fadeOut {
  -webkit-animation-name: fadeOut;
          animation-name: fadeOut;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
}
.fade-exit {
  opacity: 1;
}
.fadeIn {
  -webkit-animation-name: fadeIn;
          animation-name: fadeIn;
     -webkit-animation-duration: 0.3s;
             animation-duration: 0.3s;
}

@-webkit-keyframes fadeIn {
  from {
      opacity: 0;
  }

  to {
    opacity: 1;
    left: 0px;
  }
}

@keyframes fadeIn {
  from {
      opacity: 0;
  }

  to {
    opacity: 1;
    left: 0px;
  }
}
@-webkit-keyframes fadeOut {
  from {
      opacity: 1;
  }

  to {
    opacity: 0;
    left: -10px;
  }
}
@keyframes fadeOut {
  from {
      opacity: 1;
  }

  to {
    opacity: 0;
    left: -10px;
  }
}
